/**
  Each row is 136 pixels, so add an extra row depending on screen size.
  Is there a better way of doing this??
*/
@media only screen and (min-height: 808px) {
    .Villagers {
        max-height: calc(136px * 2);
    }
}

@media only screen and (max-width: 768px) {
    .bc-container {
        align-items: center;
        margin-bottom: 5%;
    }

    .ClearButtons {
        margin-bottom: 5%;
    }
}

@media only screen and (max-width: 730px) {
    .bc-container {
        flex-direction: column;
        align-items: center;
    }

    .btn-group {
        padding-left: 0;
    }

    .ClearButtons {
        margin-bottom: 5%;
    }
}

@media only screen and (max-width: 320px) {
    .btn-group {
        grid-gap: 0.1rem;
        grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
        padding-left: 0;
        margin-top: 5%;
    }

    .ClearButtons {
        margin-bottom: 5%;
    }
}

@media only screen and (min-height: 944px) {
    .Villagers {
        max-height: calc(136px * 3);
    }
}

@media only screen and (min-height: 1080px) {
    .Villagers {
        max-height: calc(136px * 4);
    }
}

@media only screen and (min-height: 1216px) {
    .Villagers {
        max-height: calc(136px * 5);
    }
}

@media only screen and (min-height: 1352px) {
    .Villagers {
        max-height: calc(136px * 6);
    }
}

@media only screen and (max-width: 1242px) {
